import { Redirect } from "react-router-dom";
import FileManager from "./FileManager";
import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import PrimaryAppBar from './AppBar';
import host, { token, getRefreshToken, wsHost, downloadFiles } from "../api";

import { Paper, Snackbar } from "@material-ui/core";
import useDownloader from 'react-use-downloader';
import { Alert } from "@material-ui/lab";
import Upload from "./Upload";
import QRCode from "react-qr-code";
import Sidebar from "./Sidebar";

const useStyles = makeStyles((theme) => ({
    rootHome: {
        width: "100%",
        display: 'flex',
        minHeight: "100vh"
    },
    content: {
        flexGrow: 1,
        overflow: "auto",
        padding: theme.spacing(3),
    },
    main:{
        display: "grid",
        width: "100%",
        gridTemplateColumns: "85% 15%"
    }
}));



function Home() {
    const classes = useStyles();

    const [user, setUser] = useState({})
    const [msg, setMsg] = useState("")
    const [open, setOpen] = useState(false)
    const [path, setPath] = React.useState("")

    const { size, elapsed, percentage, download, cancel, error, isInProgress } =
        useDownloader({
            // mode: 'no-cors',
            // credentials: 'include',
            headers: {
                Authorization: 'Bearer ' + token,
            },
        });

    const sessionStart = () => {
        const socket = new WebSocket("ws://" + wsHost + "/session?token=" + token);

        socket.addEventListener('open', function (event) {
            socket.send("JSON.stringify()");
            console.log("open")

        });

        socket.addEventListener('message', function (event) {

            console.log('Received message from server:', event.data);
            let json = JSON.parse(event.data)
            if (json.notify) {
                notify(json.msg)
            }
            if (json.files) {
                downloadFiles(json.files)
                // downloadFileAtURL(json.files[0])
            }

        });

        socket.addEventListener('close', function (event) {
            console.log('Connection closed:', event);
            notify("Connection closed")

        });
    }
    const notify = (text) => {
        setMsg(text)
        setOpen(true)
    }

    React.useEffect(() => {

        sessionStart()
    }, [])


    // Retrieve token from local storage
    const savedToken = sessionStorage.getItem('token');
    if (!savedToken) {
        return <Redirect to="/login" />;
    } else {
        // getRefreshToken()
    }
    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
            return;
        }

        setOpen(false);
    };

    return (
        <Paper className={classes.rootHome}>

            <CssBaseline />
            <PrimaryAppBar />
            <div className={classes.main}>
                <FileManager path={path} setPath={setPath} />
                <Sidebar/>
            </div>

           
            {/* <QRCode
                title="GeeksForGeeks"
                value=""
                bgColor="#FFFFFF"
                fgColor="#000000"
                level='Q'
            /> */}

            <Snackbar open={open} autoHideDuration={6000} onClose={handleClose}>
                <Alert onClose={handleClose} severity="success">
                    {msg}
                </Alert>
            </Snackbar>
        </Paper>
    );
}

export default Home;